<template>
  <div class="critical_illness">
    <p class="critical_illness-title">แผนความคุ้มครองโรคร้ายที่คุณต้องการ</p>
    <div class="tabMain">
      <span :class="defaultMain == index ? 'checkTabMain' : 'uncheckTabMain'" v-for="(item,index) in tabButtonText" :key="index" @click="switchMain(index)">
        {{ item.textName }}
      </span>
    </div>
    <div class="baseMain" v-show="baseMainShow">
      <!-- <p class="critical_illness-title">มาตรฐาน</p> -->
      <p>
        <span>1. คุณอยากเตรียมเงินสำรองต่อเดือนไว้เท่าไรเพื่อที่จะคงสภาพคุณภาพชีวิตที่ดีของคุณในปัจจุบัน </span>
        <span class="directionText">(กรณีเกิดโรคร้ายแรง)</span>
        <aia-input type="number" v-model="reserveMonthly"></aia-input>
      </p>
      <p>
        <span>2. คุณอยากเตรียมเงินสำรองไว้สำหรับค่ารักษาพยาบาลเท่าไร    (กรณีเกิดโรคร้ายแรง)</span>
        <!-- <span class="directionText">(กรณีมีค่าใช้จ่ายเพิ่มเติมสำหรับการพักรักษาตัว เช่น ค่าอุปกรณ์การแพทย์ ค่าพยาบาลพิเศษ)</span> -->
        <aia-input type="number" v-model="reserveMedical"></aia-input>
      </p>
      <p>
        <span>3. คุณคิดว่าต้องใช้เวลาประมาณการเพื่อพักฟื้นเนื่องจากโรคร้ายแรงกี่ปี</span>
        <!-- <span class="directionText">(กรณีชดเชยรายได้เนื่องจากอุบัติเหตุ)</span> -->
        <aia-input type="number" v-model='recoveryPeriod'></aia-input>
      </p>
      <p>
          <span>4. เพิ่มความพร้อม</span>
          <aia-input disabled :value="addPreparation"></aia-input>
        </p>
      <p class="tag-title"><label>คลิกที่นี่</label>เพื่อดูตัวอย่างค่าใช้จ่ายโรคร้ายแรง</p>
    </div>
    <div class="seniorMain" v-show="seniorMainShow">
      <p>
        <span>1. คุณอยากเตรียมเงินไว้ให้คนข้างหลังเท่าไร</span>
        <aia-input type="number" v-model="allPreparationSort"></aia-input>
      </p>
      <p>
        <span>2. คุณเตรียมไว้แล้วเท่าไร</span>
        <aia-input type="number" v-model="existingPreparation"></aia-input>
      </p>
      <p>
        <span>3. คุณต้องเตรียมเพิ่มอีก</span>
        <aia-input type="text" disabled :value="addPreparationSort"></aia-input>
      </p>
      <p class="tag-title"><label>คลิกที่นี่</label>เพื่อดูตัวอย่างค่าใช้จ่ายโรคร้ายแรง</p>
    </div>
  </div>
</template>
<script>
export default {
  nanme:'criticalIllness',
  data(){
    return {
      defaultMain:0,
      baseMainShow:true,
      seniorMainShow:false,
      tabButtonText:[{
        textName : 'แบบมาตรฐาน'
      },{
        textName : 'ยิ่งขึ้น'
      }],
      reserveMonthly:'',
      reserveMedical:'',
      recoveryPeriod:'',
      //sort
      allPreparationSort:'',
      existingPreparation:''
    }
  },
  methods:{
    switchMain(index){
      this.defaultMain = index;
      if(index == 0){
        this.baseMainShow = true;
        this.seniorMainShow = false;
      }else{
        this.baseMainShow = false;
        this.seniorMainShow = true;
        this.allPreparationSort = Number([(this.reserveMonthly * 12) * Number(this.recoveryPeriod)]) + Number(this.reserveMedical);
      }
    }
  },
  computed:{
    addPreparation(){
      let addPreparation = [(this.reserveMonthly * 12) + Number(this.reserveMedical)] * this.recoveryPeriod;
      if(addPreparation < 0){
        addPreparation = 0;
      }
      return addPreparation;
    },
    addPreparationSort(){
      let addPreparationSort = this.allPreparationSort - this.existingPreparation;
      if(addPreparationSort < 0){
        addPreparationSort = 0;
      }
      return addPreparationSort;
    }
  }
};
</script>
